import "./index.scss";
import React, { Component } from "react"
import { NavLink } from "react-router-dom"
import { Badge } from "antd-mobile"
export const foots = [
    { text: "首页", path: "/main/home", name: "home", icon: "icon-home" },
    {
        text: "商品", path: "/main/classify", name: "classify", icon: "icon-dibudaohanglan-"
    },
    { text: "购物车", path: "/main/cart", name: "cart", icon: "icon-gouwuche1" },
    { text: "我的", path: "/main/mine", name: "mine", icon: "icon-gerenzhongxin1" },

]


export class Foot extends Component {
    state = {
        foots
    }
    render() {
        return (
            <footer>
                {
                    this.state.foots.map((item, i) => {
                        return (
                            <div key={i}>
                                <NavLink
                                    activeClassName="nav-active"
                                    to={item.path}>
                                    <i className={"iconfont  " + item.icon}></i>
                                    {
                                        i === this.state.foots.length - 1 && <Badge >
                                            <span className="sup" />
                                        </Badge>

                                    }
                                    <span>{item.text}</span>
                                </NavLink>
                            </div>
                        )
                    })
                }
            </footer>
        )
    }
}